body {
  background: #000;
}
#main {
  max-width: 400px;
  margin: 20px auto;
  padding: 20px;
  color: #fff;
}

.hidden {
  display: none;
}

button {
  width: 40%;
  margin: 5%;
  padding: 15px;
  border-radius: 10px;
  font-size: 18px;
  color: #000;
}

#hints img {
  width: 100%;
  margin: auto;
}

#correctWrong {
  display: flex;
  justify-content: space-around;
  margin: 40px;
}

#correctWrong button {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  border: 2px #fff solid;
  font-size: 40px;
}

#correct {
  background: #0f0;
  color: #fff;
}

#wrong {
  background: #f00;
  color: #fff;
}
